import get from "./get";
import { append } from "./insert";
/**
 * dom
 *
 *
 * 
 * ```javascript
 *   import { create } from '@tomato-js/element'
 *   // divbody
 *   create("div");
 *   // pidididabc
 *   create("p","id","abc")
 * ```
 *
 * @param tagName - 
 * @param idName - id
 * @param parent - document.body
 * @returns dom
 */
export default function create(tagName, idName, parent) {
    const element = document.createElement(tagName);
    const TagMap = {
        a: { href: "#", hideFocus: !0 },
        style: { type: "text/css" },
        link: { type: "text/css", rel: "stylesheet" },
        iframe: { frameBorder: 0 },
        script: { defer: !0, type: "text/javascript" }
    };
    const config = TagMap[tagName.toLowerCase()];
    const createdElement = Object.assign(element, config);
    if (idName)
        createdElement.id = idName;
    if (!parent) {
        return createdElement;
    }
    const parentNode = get(parent);
    if (parentNode) {
        append(parentNode, createdElement);
    }
    else {
        append(document.body, createdElement);
    }
    return createdElement;
}
